body{
  background-image: url("./bmi.jpg");
  background-repeat: no-repeat;
  background-attachment:fixed;
  background-clip: border-box;
  
}
.container{
  width: 800px;
  height: 700px;
  margin: 120px auto;
  border: 1px solid snow;
  border-radius: 10px;
  box-shadow: 15px 15px 100px  snow;
}
h1{
  text-align: center;
  color: darkred;
  text-shadow: 2px gray;
  font-family: 'Times New Roman', Times, serif;
}
label{
  font-size: 50px;
  font-family: 'Times New Roman', Times, serif;
  margin: 80px;
  padding: 2px;
  user-select: none;
}
input{
  padding: 10px;
  margin: 30px;
  outline: none;
  border-radius: 10px;
  width: 300px;
  height: 30px;
  font-size: large;
  user-select: none;
}
.btn{
  width: 700px;
  height: 50px;
  align-items: center;
  margin: 50px;
  cursor: pointer;
  outline: none;
  border-radius: 5px;
  background-color: gray;
  font-size: 30px;
  letter-spacing: 10px;
  color: antiquewhite;
}
h3{
  font-size: 50px;
  font-family: 'Times New Roman', Times, serif;
  letter-spacing: 2px;
  color: rgb(73, 40, 218);
  text-align: center;
}
span{
  color: red;
}
p{
  color: rgb(221, 72, 27);
  font-size: 40px;
  text-align: center;
  background-color: black;
}
